body {
  font-family: sans-serif;
}

.outer {
  /* display: flex;
  width: 100%;
  height: 100vh;
  position: relative; */
  position: absolute;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  background-color: #d5d5d5;
  display: flex;
  justify-content: center;
  align-items: center;
}

.inner {
  width: 90vh;
  height: 90vh;
  margin: auto;
  position: relative;
}

.author {
  width: 90vh;
  position: absolute;
  top: 0; 
  box-sizing: border-box;

}

.q, .back {
  width: 30vh;
  height: 30vh;
  position: absolute;
  transition: 1s;
  backface-visibility: hidden;
  background: white;
  box-sizing: border-box;
  outline: 1px solid black;
  cursor: pointer;
}

.q {
  z-index: 10;
}

.back {
  z-index: 1;
  display: flex;
}

/* .front img {
  max-width: 100%;
  min-width: 100%;
  height: auto;
} */
.inner .back {
	transform: rotateY(360deg);}

/* .inner .back1 {
  transform: rotateY(180deg);
} */

.inner .q1.x,
.inner .q2.x,
.inner .q3.x,
.inner .q4.x,
.inner .q5.x,
.inner .q6.x,
.inner .q7.x,
.inner .q8.x,
.inner .q9.x {
  transform: rotateY(180deg);
}


.back-inner {
  margin: auto;
  font-size: 48px;
}

.tomato {
  background-color: tomato;
}

.lightgreen {
  background-color: lightgreen;
}


.inner .back1.y,
.inner .back2.y,
.inner .back3.y,
.inner .back4.y,
.inner .back5.y,
.inner .back6.y,
.inner .back7.y,
.inner .back8.y,
.inner .back9.y {
  opacity: 0;
}